import React from 'react';

interface Props {
  section: {
    background_color: string;
    background_color_dark: string;
    title: string;
    title_color: string;
    title_color_dark: string;
    keyword: string;
    keyword_color: string;
    keyword_color_dark: string;
    description: string;
    description_color: string;
    description_color_dark: string;
    align: 'right' | 'left' | 'alternate';
    cards: {
      icon: string;
      icon_color: string;
      icon_color_dark: string;
      title: string;
      title_color: string;
      title_color_dark: string;
      description: string;
      description_color: string;
      description_color_dark: string;
    }[];
  };
}

const Section = ({ section }: Props) => (
  <div
    className={`py-16 md:py-32 bg-${section.background_color} dark:bg-${section.background_color_dark}`}
  >
    <div className="container">
      <h1
        className={`text-5xl sm:text-6xl md:text-9xl text-${section.title_color} dark:text-${section.title_color_dark}`}
      >
        {section.title}{' '}
        <b className={`text-${section.keyword_color} dark:text-${section.keyword_color_dark}`}>
          {section.keyword}
        </b>
      </h1>
      <p
        className={`text-${section.description_color} dark:text-${section.description_color_dark}`}
      >
        {section.description}
      </p>
      <div className="py-12">
        {section.cards.map((card, i) => {
          const reverse = section.align == 'left' || (section.align == 'alternate' && i % 2 == 0);
          return (
            <div className={`py-8 md:flex md:py-12 ${reverse && 'md:flex-row-reverse'}`}>
              <div className="my-4 md:flex-1">
                <img
                  src={card.icon}
                  className={`icon-${card.icon_color} dark:icon-${card.icon_color_dark} mx-auto max-h-24`}
                />
              </div>
              <div className="text-center md:flex-1 md:text-left">
                <h2
                  className={`text-3xl text-${card.title_color} dark:text-${card.title_color_dark}`}
                >
                  {card.title}
                </h2>
                <p
                  className={`text-${card.description_color} dark:text-${card.description_color_dark} max-w-lg`}
                >
                  {card.description}
                </p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  </div>
);

export default Section;
