import React, { useState } from 'react';
import getNewsletterURI from '../../utils/getNewsletterUri';
import Notification from '../notification';

interface Props {
  locale: string;
  title: string;
  intro: string;
  news: {
    title: string;
    description: string;
    link: string;
    image: string;
  };
}

const Hero = ({ locale, title, intro, news }: Props) => {
  const [email, setEmail] = useState('');
  const [honeypotEmail, setHoneypotEmail] = useState(undefined);
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(undefined);

  const handleClose = () => {
    setSuccess(false);
    setError(false);
  };

  const handleSubmit = async evt => {
    try {
      if (honeypotEmail) return;
      const newsletterURI = getNewsletterURI();
      setLoading(true);
      evt.preventDefault();
      setError(undefined);
      const { status } = await fetch(`${newsletterURI}/${locale}/contact`, {
        method: 'POST',
        headers: { accept: 'application/json', 'content-type': 'application/json' },
        body: JSON.stringify({ email }),
      });
      setSuccess(status >= 200 && status < 300);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      setError(error);
    }
  };
  return (
    <>
      <Notification
        onClose={handleClose}
        locale={locale}
        error={error}
        loading={loading}
        success={success}
      />
      <div
        className="container min-h-screen md:flex md:justify-between md:align-center md:pt-24"
        id="hero"
      >
        <div className="pt-32 mx-auto md:flex-1 md:pt-16">
          <h1 className="text-6xl text-blue font-display text-center dark:text-white md:text-left md:text-8xl">
            {title}
          </h1>
          <p className="text-justify text-blue-light dark:text-grey">{intro}</p>
          <div className="py-10 md:flex">
            <form id="newsletter_form" method="POST" onSubmit={handleSubmit} className="flex-1">
              <input
                type="email"
                name="email"
                id="email"
                placeholder="Email"
                required
                className="w-full p-2 mr-1 rounded focus:ring-2 focus:ring-blue md:flex-1"
                onChange={event => setEmail(event.target.value)}
              />
              <input
                className="hidden"
                autoComplete="off"
                type="email2"
                id="email2"
                name="email2"
                placeholder="Your e-mail here"
                onChange={event => setHoneypotEmail(event.target.value)}
              />
            </form>
            <button
              disabled={!email || honeypotEmail || success}
              type="submit"
              form="newsletter_form"
              value="Submit"
              className="bg-blue text-white p-2 mt-2 rounded max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110 disabled:bg-blue-light disabled:cursor-not-allowed disabled:transform-none md:flex-1 md:mt-0 md:ml-2"
            >
              Join newsletter
            </button>
          </div>
        </div>
        <div className="flex container md:flex-1">
          <div
            className="border-2 border-blue rounded h-80 my-8 shadow mr-2 w-48 md:h-96 md:w-72 md:ml-24"
            id="news-card"
          >
            <div className="h-3/6">
              <img src={news[0].image} className="h-full w-full object-cover rounded" />
            </div>
            <div className="p-2">
              <h3 className="text-blue dark:text-grey font-semibold">{news[0].title}</h3>
              <p className="truncate dark:text-grey-light text-sm md:whitespace-normal">
                {news[0].description}
              </p>
              <a
                target="_blank"
                rel="noopener noreferrer"
                className="text-xs absolute mt-8 text-blue-light md:t-8"
                href={news[0].link}
              >
                {news[0].link}
              </a>
            </div>
          </div>
          <div className="my-8 w-8 flex flex-col justify-between md:max-h-96 md:mx-8">
            <div id="credibility" className="bg-blue w-24 h-24 rounded md:w-32 md:h-28">
              <h6 className="text-grey text-center pt-2">Credibility</h6>
              <h6 className="text-white text-center pt-6 text-5xl font-display">
                {news[0].credibility}%
              </h6>
            </div>
            <div id="sources" className="bg-blue-light w-24 h-24 rounded md:w-32 md:h-28">
              <h6 className="text-grey text-center pt-2">Similar on</h6>
              <ul className="text-white p-2 md:p-4">
                <li> - BFM TV</li>
                <li> - LCI </li>
              </ul>
            </div>
            <div id="virality" className="border-2 border-grey w-24 h-24 rounded md:w-32 md:h-28">
              <h6 className="text-grey text-center pt-2">Virality</h6>
              <h6 className="text-white text-center pt-4 text-2xl">High</h6>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Hero;
