import React from 'react';

interface Props {
  useCases: {
    thumbnail: string;
    title: string;
    slug: string;
  }[];
  section: {
    background_color: string;
    background_color_dark: string;
    title: string;
    title_color: string;
    title_color_dark: string;
    keyword: string;
    keyword_color: string;
    keyword_color_dark: string;
    description: string;
    description_color: string;
    description_color_dark: string;
  };
}

const UseCases = ({ useCases, section }: Props) => (
  <div
    className={`py-16 md:py-32 bg-${section.background_color} dark:bg-${section.background_color_dark}`}
  >
    <div className="container">
      <h1
        className={`text-5xl sm:text-6xl md:text-9xl text-${section.title_color} dark:text-${section.title_color_dark}`}
      >
        {section.title}{' '}
        <b className={`text-${section.keyword_color} dark:text-${section.keyword_color_dark}`}>
          {section.keyword}
        </b>
      </h1>
      <p
        className={`text-${section.description_color} dark:text-${section.description_color_dark}`}
      >
        {section.description}
      </p>
      <div className="py-12 md:flex md:justify-between">
        {useCases.map(useCase => (
          <a href={useCase.slug} className="h-64 w-52 my-8 mx-auto relative md:h-72 md:w-64 block">
            <div className="bg-blue absolute h-full w-full bg-opacity-30"></div>
            <h3 className="text-white absolute font-display text-4xl text-center w-full pt-8 md:text-5xl">
              {useCase.title}
            </h3>
            <img src={useCase.thumbnail} className="object-cover h-full w-full rounded shadow" />
          </a>
        ))}
      </div>
    </div>
  </div>
);

export default UseCases;
