import React from 'react';

interface Props {
  stats: {
    title: string;
    title_color: string;
    title_color_dark: string;
    number: string;
    number_color: string;
    number_color_dark: string;
    description: string;
    description_color: string;
    description_color_dark: string;
  }[];
}

const Stats = ({ stats }: Props) => (
  <div className="container md:flex md:flex-wrap md:justify-between" id="stats">
    {stats.map(stat => (
      <div className="my-8 max-w-xs text-center mx-auto md:mx-0 md:p-8">
        <h2 className={`text-6xl text-${stat.title_color} dark:text-${stat.title_color_dark}`}>
          {stat.title}
        </h2>
        <h4 className={`text-9xl text-${stat.number_color} dark:text-${stat.number_color_dark}`}>
          {stat.number}
        </h4>
        <p
          className={`text-justify text-${stat.description_color} dark:text-${stat.description_color_dark}`}
        >
          {stat.description}
        </p>
      </div>
    ))}
  </div>
);

export default Stats;
